@import url("https://fonts.googleapis.com/css2?family=Lemon&family=Roboto:wght@100;400;500;700&family=Source+Sans+Pro:wght@300;400;700;900&display=swap");

.App {
  font-family: "Lemon Sans";
}
.header-container {
  max-width: 345px;
  margin: 0 auto;
  padding: 0 15px;
}
.header-inner-section {
  display: flex;
  justify-content: space-between;
  padding: 13px 0 13px 0;
}
.header-leftpart {
  flex-basis: 23px;
}
.header-leftpart img {
  width: 100%;
}
.header-middlepart {
  font-family: "Lemon Sans";
  font-weight: 400;
  font-size: 24px;
  line-height: 27px;
  text-align: center;
  letter-spacing: 0.05em;
  color: #3f1569;
}
.header-carticon {
  position: relative;
  display: inline-block;
}
.header-carticon-amount {
  display: flex !important;
  justify-content: center;
  align-items: center;
  font-size: 15px;
  right: 9px;
  top: 0;
  width: 11px;
  height: 11px;
  background: #ffa31a;
  display: inline-block;
  position: absolute;
  border-radius: 11px;
}
